import { Component, OnInit, Input } from '@angular/core';
// 
import { ActivatedRoute } from '@angular/router';
// 
import { Location } from '@angular/common';
import { UniversityService } from '../university.service';

@Component({
  selector: 'app-add',
  templateUrl: './add-graduateProjectCategory.component.html',
  styleUrls: ['./add-graduateProjectCategory.component.css']
})
// 
export class AddGraduateProjectCategoryComponent implements OnInit {
  // @ college
  @Input() graduateProjectCategory: { no: string; description: string; remarks: string };
  //  
  constructor(
    private route: ActivatedRoute,
    private graduateProjectCategoryService: UniversityService,
    private location: Location
  ) {}

  ngOnInit(): void {}
  // college1id)college
  save(description: string, no: string, remarks: string): void { // 
    this.graduateProjectCategory = {no: no.trim(), description: description.trim(), remarks: remarks.trim()};
    if (!description) { return; }
    // servlet
    this.graduateProjectCategoryService.addGraduateProjectCategory(this.graduateProjectCategory)
      .subscribe(() => this.goBack());
  }

  goBack(): void {
    this.location.back();
  }
}
