import { Component, OnInit, Input } from '@angular/core';
// 
import { ActivatedRoute } from '@angular/router';
// 
import { Location } from '@angular/common';
import { UniversityService } from '../university.service';

@Component({
  selector: 'app-add',
  templateUrl: './add-graduateProjectStatus.component.html',
  styleUrls: ['./add-graduateProjectStatus.component.css']
})
// 
export class AddGraduateProjectStatusComponent implements OnInit {
  // @ college
  @Input() graduateProjectStatus: { no: string; description: string; remarks: string };
  //  
  constructor(
    private route: ActivatedRoute,
    private graduateProjectStatusService: UniversityService,
    private location: Location
  ) {}

  ngOnInit(): void {}
  // college1id)college
  save(description: string, no: string, remarks: string): void { // 
    this.graduateProjectStatus = { no: no.trim(), description: description.trim(), remarks: remarks.trim()};
    if (!description) { return; }
    // servlet
    this.graduateProjectStatusService.addGraduateProjectStatus(this.graduateProjectStatus)
      .subscribe(() => this.goBack());
  }

  goBack(): void {
    this.location.back();
  }
}
