import {Component, DoCheck, OnInit} from '@angular/core';
import { GraduateProjectStatus } from '../entity';
import { UniversityService } from '../university.service';

@Component({
  // tslint:disable-next-line:component-selector
  selector: ' app-graduateProjectStatus',
  templateUrl: './graduateProjectStatus.component.html',
  styleUrls: ['./graduateProjectStatus.component.css']
})
export class GraduateProjectStatusComponent implements OnInit, DoCheck {
  graduateProjectStatuss: GraduateProjectStatus[];
  private dataShow: GraduateProjectStatus[];      // 
  public leng: number;                 // ()
  public currentPag: number;           // 
  public pagNums: number;              // 
  public oldCurrentPag: number;
  public oldPagNums: number;
  constructor(private graduateProjectStatusService: UniversityService) { }

  ngOnInit() {
    this.getGraduateProjectStatuss();
  }
  // DoCheck DoCheck
  ngDoCheck(): void {
    if (this.currentPag !== this.oldCurrentPag || this.pagNums !== this.oldPagNums) {
      this.dataShow = [];
      let startNum = (this.currentPag - 1) * this.pagNums;
      for ( let i = 0; i < this.pagNums; i++, startNum++) {
        if ( this.graduateProjectStatuss[startNum] !== null && this.graduateProjectStatuss[startNum] !== undefined) {
          this.dataShow.push(this.graduateProjectStatuss[startNum]);
        }
      }
      this.oldCurrentPag = this.currentPag;
      this.oldPagNums = this.pagNums;
    }
  }
// 
  currentPagHandel(pag: number) {
    this.currentPag = pag;
    console.log( 'curpag:' + pag );
  }
  // 
  pagNumsHandel(pagNum: number) {
    this.pagNums = pagNum;
  }
  getGraduateProjectStatuss(): void {
    this.graduateProjectStatusService.getGraduateProjectStatuss()
      .subscribe(res => this.graduateProjectStatuss = res);
  }
  delete(graduateProjectStatus: GraduateProjectStatus | number): void {
    this.graduateProjectStatusService.deleteGraduateProjectStatus(graduateProjectStatus)
      .subscribe(res => {
        alert(res.message); // 
        // 
        // if ( res.message === '') { //
        this.graduateProjectStatuss = this.graduateProjectStatuss.filter(h => h !== graduateProjectStatus); // 
        this.dataShow = [];
        let startNum = (this.currentPag - 1) * this.pagNums;
        for ( let i = 0; i < this.pagNums; i++, startNum++) {
          if ( this.graduateProjectStatuss[startNum] !== null && this.graduateProjectStatuss[startNum] !== undefined) {
            this.dataShow.push(this.graduateProjectStatuss[startNum]);
          }
        }
      });
  }
}
