import {Component, DoCheck, OnInit} from '@angular/core';
import {College} from '../entity';
import {UniversityService} from '../university.service';

@Component({
  selector: 'app-college',
  templateUrl: './college.component.html',
  styleUrls: ['./college.component.css']
})
export class CollegeComponent implements OnInit, DoCheck {
  colleges: College[];
  private dataShow: College[];      // 
  public leng: number;                 // ()
  public currentPag: number;           // 
  public pagNums: number;              // 
  public oldCurrentPag: number;
  public oldPagNums: number;
  constructor(private schoolService: UniversityService) { }

  ngOnInit() {
    this.getColleges();
  }

  // DoCheck DoCheck
  ngDoCheck(): void {
    if (this.currentPag !== this.oldCurrentPag || this.pagNums !== this.oldPagNums) {
      this.dataShow = [];
      let startNum = (this.currentPag - 1) * this.pagNums;
      for ( let i = 0; i < this.pagNums; i++, startNum++) {
        if ( this.colleges[startNum] !== null && this.colleges[startNum] !== undefined) {
          this.dataShow.push(this.colleges[startNum]);
        }
      }
      this.oldCurrentPag = this.currentPag;
      this.oldPagNums = this.pagNums;
    }
  }
// 
  currentPagHandel(pag: number) {
    this.currentPag = pag;
    console.log( 'curpag:' + pag );
  }
  // 
  pagNumsHandel(pagNum: number) {
    this.pagNums = pagNum;
  }
  getColleges(): void {
    this.schoolService.getColleges ()
      .subscribe(res => this.colleges = res);
  }

  delete(college: College | number): void {
    this.schoolService.deleteCollege(college)
      .subscribe(res => {
        alert(res.message); // 
        // 
        // if ( res.message === '') { //
        this.colleges = this.colleges.filter(h => h !== college); // 
        this.dataShow = [];
        let startNum = (this.currentPag - 1) * this.pagNums;
        for ( let i = 0; i < this.pagNums; i++, startNum++) {
          if ( this.colleges[startNum] !== null && this.colleges[startNum] !== undefined) {
            this.dataShow.push(this.colleges[startNum]);
          }
        }
      });
  }
}
