import {Component, DoCheck, OnInit} from '@angular/core';
import {Title} from '../entity';
import {UniversityService} from '../university.service';

@Component({
  selector: 'app-title',
  templateUrl: './proftitle.component.html',
  styleUrls: ['./proftitle.component.css']
})
export class ProfTitleComponent implements OnInit, DoCheck {
  titles: Title[];
  private dataShow: Title[];      // 
  public leng: number;                 // ()
  public currentPag: number;           // 
  public pagNums: number;              // 
  public oldCurrentPag: number;
  public oldPagNums: number;
  constructor(private titleService: UniversityService) { }

  ngOnInit() {
    this.getTitles();
  }
  // DoCheck DoCheck
  ngDoCheck(): void {
    if (this.currentPag !== this.oldCurrentPag || this.pagNums !== this.oldPagNums) {
      this.dataShow = [];
      let startNum = (this.currentPag - 1) * this.pagNums;
      for ( let i = 0; i < this.pagNums; i++, startNum++) {
        if ( this.titles[startNum] !== null && this.titles[startNum] !== undefined) {
          this.dataShow.push(this.titles[startNum]);
        }
      }
      this.oldCurrentPag = this.currentPag;
      this.oldPagNums = this.pagNums;
    }
  }
// 
  currentPagHandel(pag: number) {
    this.currentPag = pag;
    console.log( 'curpag:' + pag );
  }
  // 
  pagNumsHandel(pagNum: number) {
    this.pagNums = pagNum;
  }
  getTitles(): void {
    this.titleService.getTitles ()
      .subscribe(res => this.titles = res);
  }
  delete(title: Title | number): void {
    this.titleService.deleteTitle(title)
      .subscribe(res => {
        alert(res.message); // 
        // 
        // if ( res.message === '') { //
        this.titles = this.titles.filter(h => h !== title); // 
        this.dataShow = [];
        let startNum = (this.currentPag - 1) * this.pagNums;
        for ( let i = 0; i < this.pagNums; i++, startNum++) {
          if ( this.titles[startNum] !== null && this.titles[startNum] !== undefined) {
            this.dataShow.push(this.titles[startNum]);
          }
        }
      });
  }
}
