import {Component, DoCheck, OnInit} from '@angular/core';
import { GraduateProjectType } from '../entity';
import { UniversityService } from '../university.service';

@Component({
  // tslint:disable-next-line:component-selector
  selector: ' app-graduateProjectType',
  templateUrl: './graduateProjectType.component.html',
  styleUrls: ['./graduateProjectType.component.css']
})
export class GraduateProjectTypeComponent implements OnInit, DoCheck {
  graduateProjectTypes: GraduateProjectType[];
  private dataShow: GraduateProjectType[];      // 
  public leng: number;                 // ()
  public currentPag: number;           // 
  public pagNums: number;              // 
  public oldCurrentPag: number;
  public oldPagNums: number;
  constructor(private graduateProjectTypeService: UniversityService) { }

  ngOnInit() {
    this.getGraduateProjectTypes();
  }
  // DoCheck DoCheck
  ngDoCheck(): void {
    if (this.currentPag !== this.oldCurrentPag || this.pagNums !== this.oldPagNums) {
      this.dataShow = [];
      let startNum = (this.currentPag - 1) * this.pagNums;
      for ( let i = 0; i < this.pagNums; i++, startNum++) {
        if ( this.graduateProjectTypes[startNum] !== null && this.graduateProjectTypes[startNum] !== undefined) {
          this.dataShow.push(this.graduateProjectTypes[startNum]);
        }
      }
      this.oldCurrentPag = this.currentPag;
      this.oldPagNums = this.pagNums;
    }
  }
// 
  currentPagHandel(pag: number) {
    this.currentPag = pag;
    console.log( 'curpag:' + pag );
  }
  // 
  pagNumsHandel(pagNum: number) {
    this.pagNums = pagNum;
  }
  getGraduateProjectTypes(): void {
    this.graduateProjectTypeService.getGraduateProjectTypes()
      .subscribe(res => this.graduateProjectTypes = res);
  }
  delete(graduateProjectType: GraduateProjectType | number): void {
    this.graduateProjectTypeService.deleteGraduateProjectType(graduateProjectType)
      .subscribe(res => {
        alert(res.message); // 
        // 
        // if ( res.message === '') { //
        this.graduateProjectTypes = this.graduateProjectTypes.filter(h => h !== graduateProjectType); // 
        this.dataShow = [];
        let startNum = (this.currentPag - 1) * this.pagNums;
        for ( let i = 0; i < this.pagNums; i++, startNum++) {
          if ( this.graduateProjectTypes[startNum] !== null && this.graduateProjectTypes[startNum] !== undefined) {
            this.dataShow.push(this.graduateProjectTypes[startNum]);
          }
        }
      });
  }
}
