import {Component, DoCheck, OnInit} from '@angular/core';
import {GraduateProject} from '../entity';
import {UniversityService} from '../university.service';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'app-graduateProject',
  templateUrl: './graduateProject.component.html',
})
export class GraduateProjectComponent implements OnInit, DoCheck {
  graduateProjects: GraduateProject[];
  private dataShow: GraduateProject[];      // 
  public leng: number;                 // ()
  public currentPag: number;           // 
  public pagNums: number;              // 
  public oldCurrentPag: number;
  public oldPagNums: number;
  private n: number;
  constructor(private graduateProjectService: UniversityService) { }

  ngOnInit() {
    this.getGraduateProjects();
  }
  // DoCheck DoCheck
  ngDoCheck(): void {
    if (this.currentPag !== this.oldCurrentPag || this.pagNums !== this.oldPagNums) {
      this.dataShow = [];
      let startNum = (this.currentPag - 1) * this.pagNums;
      for ( let i = 0; i < this.pagNums; i++, startNum++) {
        if ( this.graduateProjects[startNum] !== null && this.graduateProjects[startNum] !== undefined) {
          this.dataShow.push(this.graduateProjects[startNum]);
        }
      }
      this.oldCurrentPag = this.currentPag;
      this.oldPagNums = this.pagNums;
    }
  }
// 
  currentPagHandel(pag: number) {
    this.currentPag = pag;
    console.log( 'curpag:' + pag );
  }
  // 
  pagNumsHandel(pagNum: number) {
    this.pagNums = pagNum;
  }
  getGraduateProjects(): void {
    this.graduateProjectService.getGraduateProjects ()
      .subscribe(res => this.graduateProjects = res);
  }
  delete(graduateProject: GraduateProject | number): void {
    this.graduateProjectService.deleteGraduateProject(graduateProject)
      .subscribe(res => {
        alert(res.message); // 
        // 
        // if ( res.message === '') { //
        this.graduateProjects = this.graduateProjects.filter(h => h !== graduateProject); // 
        this.dataShow = [];
        let startNum = (this.currentPag - 1) * this.pagNums;
        for ( let i = 0; i < this.pagNums; i++, startNum++) {
          if ( this.graduateProjects[startNum] !== null && this.graduateProjects[startNum] !== undefined) {
            this.dataShow.push(this.graduateProjects[startNum]);
          }
        }
      });
  }
}
