import {Component, DoCheck, OnInit} from '@angular/core';
import { GraduateProjectCategory } from '../entity';
import { UniversityService } from '../university.service';

@Component({
  // tslint:disable-next-line:component-selector
  selector: ' app-graduateProjectCategory',
  templateUrl: './graduateProjectCategory.component.html',
  styleUrls: ['./graduateProjectCategory.component.css']
})
export class GraduateProjectCategoryComponent implements OnInit, DoCheck {
  graduateProjectCategorys: GraduateProjectCategory[];
  private dataShow: GraduateProjectCategory[];      // 
  public leng: number;                 // ()
  public currentPag: number;           // 
  public pagNums: number;              // 
  public oldCurrentPag: number;
  public oldPagNums: number;
  constructor(private graduateProjectCategoryService: UniversityService) { }

  ngOnInit() {
    this.getGraduateProjectCategorys();
  }
  // DoCheck DoCheck
  ngDoCheck(): void {
    if (this.currentPag !== this.oldCurrentPag || this.pagNums !== this.oldPagNums) {
      this.dataShow = [];
      let startNum = (this.currentPag - 1) * this.pagNums;
      for ( let i = 0; i < this.pagNums; i++, startNum++) {
        if ( this.graduateProjectCategorys[startNum] !== null && this.graduateProjectCategorys[startNum] !== undefined) {
          this.dataShow.push(this.graduateProjectCategorys[startNum]);
        }
      }
      this.oldCurrentPag = this.currentPag;
      this.oldPagNums = this.pagNums;
    }
  }
// 
  currentPagHandel(pag: number) {
    this.currentPag = pag;
    console.log( 'curpag:' + pag );
  }
  // 
  pagNumsHandel(pagNum: number) {
    this.pagNums = pagNum;
  }
  getGraduateProjectCategorys(): void {
    this.graduateProjectCategoryService.getGraduateProjectCategorys()
      .subscribe(res => this.graduateProjectCategorys = res);
  }
  delete(graduateProjectCategory: GraduateProjectCategory | number): void {
    this.graduateProjectCategoryService.deleteGraduateProjectCategory(graduateProjectCategory)
      .subscribe(res => {
        alert(res.message); // 
        // 
        // if ( res.message === '') { //
        this.graduateProjectCategorys = this.graduateProjectCategorys.filter(h => h !== graduateProjectCategory); // 
        this.dataShow = [];
        let startNum = (this.currentPag - 1) * this.pagNums;
        for ( let i = 0; i < this.pagNums; i++, startNum++) {
          if ( this.graduateProjectCategorys[startNum] !== null && this.graduateProjectCategorys[startNum] !== undefined) {
            this.dataShow.push(this.graduateProjectCategorys[startNum]);
          }
        }
      });
  }
}
